.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    pointer-events: none;
    transition: 0.5ms ease-in-out;
}

.modal.is-open {
    opacity: 1;
    pointer-events: initial;
}

.modal_overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.5ms ease-in-out;
}


.modal_content {
    transform: scale(0);
    transition: 0.8s ease-in-out;
}

.modal.is-open .modal_content {
    transform: scale(0.7);
    transition: 0.8s ease-in-out;
}

.modal_image {
    display: block;
    width: auto;
    height: auto;
    max-height: 100vh;
    max-width: 100vw;
}

.modal_button {
    position: absolute;
    top: 30px;
    right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border: 2px solid white;
    border-radius: 50%;
    background-color: rgba(90, 90, 90, 0.5);
    cursor: pointer;
    box-shadow: rgba(255, 127, 80, 0.6) 0 7px 29px 0;
    outline: none;
    background-image: url('../public/close.svg');
    background-position: center;
    background-size: 60%;
    background-repeat: no-repeat;
    transition: 0.2s ease-in-out;
}

.modal_button:hover,
.modal_button:focus {
    background-color: coral;
    transition: 0.2s ease-in-out;
}

.img {
    box-shadow: rgba(0, 0, 0, 0.6) 0 17px 99px 0;
}

.img-card {
    object-fit: contain;
    max-width: 80vw;
    max-height: 90vh;
}